@charset "UTF-8";
body {
  color: #666;
  font-family: "Orbitron", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

:root {
  --blue: #00aae5;
  --orange: #f69b1e;
  --green: #8cc63f;
}

#wpadminbar {
  display: none;
}

html {
  margin-top: 0 !important;
}

body {
  height: 100vh;
  background-size: 10px, 100%;
}
body.gray {
  background-image: url(../assets/img/pattern2.png), linear-gradient(#f6f6f6, #ddd);
}
body.gray #root {
  background: linear-gradient(#f6f6f6, #ddd);
  color: #999;
}
@media (hover: hover) {
  body.gray #root article.dday-list ul::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #f5f5f5;
  }
}
body.gray #root article.dday-list ul::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}
body.gray #root article.dday-list ul::-webkit-scrollbar-thumb {
  background-color: rgba(153, 153, 153, 0.3);
}
body.beige {
  background-image: url(../assets/img/pattern4.png), linear-gradient(#f5eef1, #f0b8b8);
}
body.beige #root {
  background: linear-gradient(#f5eef1, #f0b8b8);
  color: rgb(189, 137, 150);
}
@media (hover: hover) {
  body.beige #root article.dday-list ul::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #f5f5f5;
  }
}
body.beige #root article.dday-list ul::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}
body.beige #root article.dday-list ul::-webkit-scrollbar-thumb {
  background-color: rgba(189, 137, 150, 0.5);
}
body.skyblue {
  background-image: url(../assets/img/pattern3.png), linear-gradient(#edf6ff, #a2cbf7);
}
body.skyblue #root {
  background: linear-gradient(#edf6ff, #a2cbf7);
  color: #4881c2;
}
@media (hover: hover) {
  body.skyblue #root article.dday-list ul::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #f5f5f5;
  }
}
body.skyblue #root article.dday-list ul::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}
body.skyblue #root article.dday-list ul::-webkit-scrollbar-thumb {
  background-color: rgba(72, 129, 194, 0.5);
}
body #root {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: min(95%, 400px);
  height: min(95%, 600px);
  overflow: hidden;
  overflow-y: auto; /* 좌우로 안 나옴 */
  box-shadow: -2px -2px 5px rgb(255, 255, 255), 3px 3px 5px rgba(0, 0, 0, 0.1);
  border-radius: 30px;
}
body #root section {
  box-sizing: border-box;
  min-height: 100%;
  padding: 15px;
}
body #root section h1,
body #root section h2 {
  margin-bottom: 20px;
  text-align: center;
  font-weight: 900;
  font-size: 1.7em;
  text-shadow: -2px -2px 1px rgb(255, 255, 255), 2px 2px 1px rgba(0, 0, 0, 0.1);
}
body #root .back-btn {
  z-index: 10;
  position: absolute;
  right: 10px;
  top: 5px;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  font-size: 1.3em;
  transform: scale(-1);
}
body #root .back-btn:hover {
  color: rqba(#fff, 0.5);
  text-shadow: -1px -1px 1px rgba(0, 0, 0, 0.5);
}

@keyframes cursor {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}