body {
  color: #666;
  font-family: "Orbitron", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

:root {
  --blue: #00aae5;
  --orange: #f69b1e;
  --green: #8cc63f;
}

#wpadminbar {
  display: none;
}

html {
  margin-top: 0 !important;
}

body {
  height: 100vh;
  background-size: 10px, 100%; //앞에건 작게 뒤에건 크게
  &.gray {
    background-image: url(../assets/img/pattern2.png), linear-gradient(#f6f6f6, #ddd);
    #root {
      background: linear-gradient(#f6f6f6, #ddd);
      color: #999;

      article.dday-list ul {
        @media (hover: hover) {
          &::-webkit-scrollbar-track {
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            background-color: #f5f5f5;
          } //media hover
        } //article.dday-list ul

        &::-webkit-scrollbar {
          width: 10px;
          background-color: #f5f5f5;
        }

        &::-webkit-scrollbar-thumb {
          background-color: rgba(#999, 0.3);
          //border: 2px solid #555555;
        }
      }
    }
  } //body.grey

  &.beige {
    background-image: url(../assets/img/pattern4.png), linear-gradient(#f5eef1, #f0b8b8);

    #root {
      background: linear-gradient(#f5eef1, #f0b8b8);
      color: rgb(189, 137, 150);
      article.dday-list ul {
        @media (hover: hover) {
          &::-webkit-scrollbar-track {
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            background-color: #f5f5f5;
          } //media hover
        } //article.dday-list ul

        &::-webkit-scrollbar {
          width: 10px;
          background-color: #f5f5f5;
        }

        &::-webkit-scrollbar-thumb {
          background-color: rgba(189, 137, 150, 0.5);
          //border: 2px solid #555555;
        }
      }
    }
  } //body.pink

  &.skyblue {
    background-image: url(../assets/img/pattern3.png), linear-gradient(#edf6ff, #a2cbf7);
    #root {
      background: linear-gradient(#edf6ff, #a2cbf7);
      color: #4881c2;
      article.dday-list ul{
        @media (hover: hover) {
        &::-webkit-scrollbar-track {
          -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
          background-color: #f5f5f5;
        }//media hover
      }//article.dday-list ul
        &::-webkit-scrollbar {
          width: 10px;
          background-color: #f5f5f5;
        }

        &::-webkit-scrollbar-thumb {
          background-color: rgba(#4881c2, 0.5);
          //border: 2px solid #555555;
        }
      }
    }
  } //body.skyblue

  #root {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: min(95%, 400px);
    height: min(95%, 600px);
    overflow: hidden;
    overflow-y: auto; /* 좌우로 안 나옴 */
    box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
    border-radius: 30px;

    section {
      box-sizing: border-box;
      min-height: 100%;
      padding: 15px;
      h1,
      h2 {
        margin-bottom: 20px;
        text-align: center;
        font-weight: 900;
        font-size: 1.7em;
        text-shadow: -2px -2px 1px rgba(255, 255, 255, 1), 2px 2px 1px rgba(0, 0, 0, 0.1);
      }
    } //section
    .back-btn {
      z-index: 10;
      position: absolute;
      right: 10px;
      top: 5px;
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      font-size: 1.3em;
      transform: scale(-1); //뒤집기
      &:hover {
        color: rqba(#fff, 0.5);
        text-shadow: -1px -1px 1px rgba(0, 0, 0, 0.5);
      } //btn hover
    } //back-btn
  } //root
} //body

@keyframes cursor {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
