@use "mixin" as m;

section.dday {
    display: flex;
    flex-direction: column;
    article.dday-list {
        overflow: hidden;
        position: relative;
        display: flex;
        flex-direction: column;
        flex: 1 0 1px;
        &::before {
            pointer-events: none;
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background-image: url(../assets/img/timer-icon-bg.gif);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
            mix-blend-mode: overlay;
            opacity: 0.8;
            filter: grayscale(1) brightness(50%);
        } //before

        .filter-btn{
            z-index: 1;
            position: absolute;
            left: calc(50% - 20px);
            border-radius: 0 0 5px 10px;
            top: 0;
            width: 40px;
            height: 40px;
            color: none;
            background: rgba(0,0,0,0.7);
            transition: all .3s;
            &:hover{
                font-size: 1.3em;
            }
            i:last-child{
                display: none;
            }
            &.active{
                i:first-child{
                    display: none;
                }
                i:last-child{
                    display: block;
                }
                color: #FFF;
            }
        }
    >ul {
            /* 
            flex : 1 ; -> flex-glow 남은 넓이를 나눠 가짐 , flex:shrink:1 (부모보다 커지지 않음? 계속 늘어날 수 있도록?...), flex-basis:10px(최소 넓이 10px 가져라; 기본값(미니멈?... 이 이하로 쪼그라들지않게 지정));
            */
            border-radius: 5px;
            flex: 1 0 1px;
            overflow: auto; // flex 위처럼 적고 overflow 주면 넓이 고정됨... 부모높이를 인식함
            margin: 15px 0;
            >li{
                &.active{
                    padding: 10px;
                    background: rgba(#ffd900,0.7);
                    cursor: pointer;
                }
                &.deactive{
                    background: rgba(#000,0.3);
                    cursor: pointer;
                    color: rgba(#FFF,0.3);
                }
                background: rgba(#FFF,0.3);
                padding: 10px;
                border-bottom: 2px groove rgba(#FFF,0.3);
                div.meta{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    h3{
                        max-width: 60%;
                        font-weight: 600;
                        font-size: 1.1em;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }
                    p{
                        .time{                        
                            display: block;
                            text-align: center;
                            font-size: 0.8em;
                            small, em{
                                margin: 0 2px;
                            }
                            small{
                                font-size: 0.2em;
                                text-transform: uppercase;
                            }
                        }//time
                    }//p
                }//div.meta
                >ul.timer-output{
                    display: flex;
                    margin-top: 10px;
                    >li{
                        flex: 1;
                        padding: 5px;
                        figure{
                            position: relative;
                            margin-bottom: 7px;
                            svg{
                                transform: rotate(-90deg);
                                width: 100%;
                                circle{
                                    fill: none;
                                    stroke: white;
                                    margin-bottom: 10px;
                                    &:nth-child(1){
                                        stroke-width: 2;
                                        stroke: rgba(#FFF,0.7);
                                    }
                                    &:nth-child(2){
                                        stroke-width: 6;
                                        stroke: #FFF;
                                    }
                                }//circle
                            }//svg
                            figcaption{
                                @include m.posLT(absolute,50%,50%,-50%,-50%);
                                font-size: 1.3em;
                                font-weight: 600;
                            }
                        }//figure
                        p{
                            text-align: center;
                            font-size: 0.8em;
                            em{
                            display: block;
                            text-transform: uppercase;
                            font-weight: 600;
                            }//em
                            small{
                                    
                            }//small
                        }//p
                    }//li
                }//ul.timer-output
            }//li
        } //ul

        .filter-form{
            display: flex;
            flex-direction: column;
            justify-content: center;
            box-sizing: border-box;
            padding: 15px;
            position: absolute;
            top: 0; left: 0;
            width: 100%;
            height: 100%;
            background: rgba(#FFF,0.7);
            transform: translateY(-100%);
            transition: transform 0.5s;
            &.active{
                transform: translateY(0%)
            }
            fieldset{
                margin-bottom: 15px;
                padding: 10px;
                background: rgba(#000,0.5);
                border-radius: 5px;
                legend{
                    padding: 5px 10px;
                    background: rgba(#000, 0.9);
                    color:none;
                    border-radius: 5px;
                    font-size: 0.9em
                }//legend
                button{
                    width: 100%;
                    margin-top: 8px;
                    padding: 10px 0;
                    outline: none;
                    border-radius: 20px;
                    box-shadow: -1px -1px 3px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.3);
                    background: rgba(#FFF, 0.8);
                    &:focus {
                        background: rgba(#fff, 0.8);
                    }
                    &:active {
                        box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.3);
                    } //button:active
                }//button
                input{
                    width: 100%;
                    box-sizing: border-box;
                    padding: 10px;
                    border: none;
                    background: rgba(#fff, 0.6);
                    outline: none;
                    border-radius: 5px;
                    box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
                    &:focus {
                        background: rgba(#FFF, 0.8);
                    }
                    &::placeholder {
                        color: inherit;
                        opacity: 0.5;
                    }
    
                    &::-webkit-calendar-picker-indicator {
                        filter: invert(10%);
                        mix-blend-mode: color-burn;
                    }
                }//input
            }//fieldset
        }//filter-form

        .no-msg{
            @include m.posLT(absolute,50%,50%,-50%,-50%);
            width: 80%;
            text-align: center;
            font-weight: 600;
            font-size: 1.2em;
        }
    } //article.dday-list

    .btn-add {
        width: 100%;
        padding: 10px 0;
        outline: none;
        border-radius: 10px;
        box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
        img {
            width: 10%;
            display: inline-block;
            vertical-align: middle;
            margin-right: 10px;
            mix-blend-mode: overlay;
            filter:grayscale(1) brightness(50%);
        }
        &:focus {
            background: rgba(#fff, 0.1);
        }
        &:active {
            box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
            //원래 값은 왼쪽 위모서리 기준으로 들어가는데 inset을 적용해서 오른쪽 구석 안쪽에 그림자 들어감
        } //button:active
    } //btn-add

    form.dday-add {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;

        p {
            padding: 15px 0;
            border-top: 2px groove rgba(#fff, 0.5);
            i {
                padding: 0 10px;
            }
            label {
                display: block;
                margin-bottom: 10px;
            }
            input {
                width: 100%;
                box-sizing: border-box;
                padding: 10px;
                border: none;
                background: rgba(#fff, 0.4);
                outline: none;
                box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
                &:focus {
                    background: rgba(#fff, 0.8);
                }
                &::placeholder {
                    color: inherit;
                    opacity: 0.5;
                }

                &::-webkit-calendar-picker-indicator {
                    filter: invert(10%);
                    mix-blend-mode: color-burn;
                }
            } //input
        } //p

        button {
            width: 100%;
            margin-top: 15px;
            padding: 10px 0;
            outline: none;
            border-radius: 20px;
            box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
            &:focus {
                background: rgba(#fff, 0.1);
            }
            &:active {
                box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
            } //button:active
        } //button

        .fa-arrow-left{
            font-size: 1.5em;
            display: block;
            margin: 0 auto;
            cursor: pointer;
            margin-top: 20px;
            transition: color 0.8s;
            &:active{
                transform: scale(1.2);
            }
            &:hover{
                color: #FFF;
            }
        }
    } //form.dday-add

    div.now {
        text-align: center;
        padding: 10px;
        background: rgba(#000, 0.7);
        border-radius: 7px;
        color: #fff;
        time {
            display: block;
            b,
            small {
                margin: 0 3px;
            }
            small {
                font-size: 0.9em;
                font-weight: 600;
                text-transform: uppercase;
                opacity: 0.8;
            }
        }
    }
} //section.dday
