@use 'mixin' as m;

figure.coach-mark{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0; left: 0;
    background: #5f5f5f;
    &.active{
        opacity: 0;
        transition: opacity 0.5s;
    }
    img{

        @include m.objectFit(contain)
    }
    figcaption{
        width: 100%;
        @include m.posLB(absolute,0,3%,0,0);
        text-align: center;
        color: #FFF;
        button{
            margin-top: 5px;
            padding: 10px 40px;
            border: 1px solid #FFF;
            text-transform: uppercase;
            transition: all 0.3s;
            background: rgba(#FFF,.2);
            &:hover, &:active{
                background: #FFF;
                color: #111;
            }
        }
    }
}