@charset "UTF-8";
section.dday {
  display: flex;
  flex-direction: column;
}
section.dday article.dday-list {
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1 0 1px;
}
section.dday article.dday-list::before {
  pointer-events: none;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../assets/img/timer-icon-bg.gif);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  mix-blend-mode: overlay;
  opacity: 0.8;
  filter: grayscale(1) brightness(50%);
}
section.dday article.dday-list .filter-btn {
  z-index: 1;
  position: absolute;
  left: calc(50% - 20px);
  border-radius: 0 0 5px 10px;
  top: 0;
  width: 40px;
  height: 40px;
  color: none;
  background: rgba(0, 0, 0, 0.7);
  transition: all 0.3s;
}
section.dday article.dday-list .filter-btn:hover {
  font-size: 1.3em;
}
section.dday article.dday-list .filter-btn i:last-child {
  display: none;
}
section.dday article.dday-list .filter-btn.active {
  color: #FFF;
}
section.dday article.dday-list .filter-btn.active i:first-child {
  display: none;
}
section.dday article.dday-list .filter-btn.active i:last-child {
  display: block;
}
section.dday article.dday-list > ul {
  /* 
  flex : 1 ; -> flex-glow 남은 넓이를 나눠 가짐 , flex:shrink:1 (부모보다 커지지 않음? 계속 늘어날 수 있도록?...), flex-basis:10px(최소 넓이 10px 가져라; 기본값(미니멈?... 이 이하로 쪼그라들지않게 지정));
  */
  border-radius: 5px;
  flex: 1 0 1px;
  overflow: auto;
  margin: 15px 0;
}
section.dday article.dday-list > ul > li {
  background: rgba(255, 255, 255, 0.3);
  padding: 10px;
  border-bottom: 2px groove rgba(255, 255, 255, 0.3);
}
section.dday article.dday-list > ul > li.active {
  padding: 10px;
  background: rgba(255, 217, 0, 0.7);
  cursor: pointer;
}
section.dday article.dday-list > ul > li.deactive {
  background: rgba(0, 0, 0, 0.3);
  cursor: pointer;
  color: rgba(255, 255, 255, 0.3);
}
section.dday article.dday-list > ul > li div.meta {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
section.dday article.dday-list > ul > li div.meta h3 {
  max-width: 60%;
  font-weight: 600;
  font-size: 1.1em;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
section.dday article.dday-list > ul > li div.meta p .time {
  display: block;
  text-align: center;
  font-size: 0.8em;
}
section.dday article.dday-list > ul > li div.meta p .time small, section.dday article.dday-list > ul > li div.meta p .time em {
  margin: 0 2px;
}
section.dday article.dday-list > ul > li div.meta p .time small {
  font-size: 0.2em;
  text-transform: uppercase;
}
section.dday article.dday-list > ul > li > ul.timer-output {
  display: flex;
  margin-top: 10px;
}
section.dday article.dday-list > ul > li > ul.timer-output > li {
  flex: 1;
  padding: 5px;
}
section.dday article.dday-list > ul > li > ul.timer-output > li figure {
  position: relative;
  margin-bottom: 7px;
}
section.dday article.dday-list > ul > li > ul.timer-output > li figure svg {
  transform: rotate(-90deg);
  width: 100%;
}
section.dday article.dday-list > ul > li > ul.timer-output > li figure svg circle {
  fill: none;
  stroke: white;
  margin-bottom: 10px;
}
section.dday article.dday-list > ul > li > ul.timer-output > li figure svg circle:nth-child(1) {
  stroke-width: 2;
  stroke: rgba(255, 255, 255, 0.7);
}
section.dday article.dday-list > ul > li > ul.timer-output > li figure svg circle:nth-child(2) {
  stroke-width: 6;
  stroke: #FFF;
}
section.dday article.dday-list > ul > li > ul.timer-output > li figure figcaption {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.3em;
  font-weight: 600;
}
section.dday article.dday-list > ul > li > ul.timer-output > li p {
  text-align: center;
  font-size: 0.8em;
}
section.dday article.dday-list > ul > li > ul.timer-output > li p em {
  display: block;
  text-transform: uppercase;
  font-weight: 600;
}
section.dday article.dday-list .filter-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  padding: 15px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.7);
  transform: translateY(-100%);
  transition: transform 0.5s;
}
section.dday article.dday-list .filter-form.active {
  transform: translateY(0%);
}
section.dday article.dday-list .filter-form fieldset {
  margin-bottom: 15px;
  padding: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 5px;
}
section.dday article.dday-list .filter-form fieldset legend {
  padding: 5px 10px;
  background: rgba(0, 0, 0, 0.9);
  color: none;
  border-radius: 5px;
  font-size: 0.9em;
}
section.dday article.dday-list .filter-form fieldset button {
  width: 100%;
  margin-top: 8px;
  padding: 10px 0;
  outline: none;
  border-radius: 20px;
  box-shadow: -1px -1px 3px rgb(255, 255, 255), 3px 3px 5px rgba(0, 0, 0, 0.3);
  background: rgba(255, 255, 255, 0.8);
}
section.dday article.dday-list .filter-form fieldset button:focus {
  background: rgba(255, 255, 255, 0.8);
}
section.dday article.dday-list .filter-form fieldset button:active {
  box-shadow: inset -2px -2px 5px rgb(255, 255, 255), inset 3px 3px 5px rgba(0, 0, 0, 0.3);
}
section.dday article.dday-list .filter-form fieldset input {
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  border: none;
  background: rgba(255, 255, 255, 0.6);
  outline: none;
  border-radius: 5px;
  box-shadow: inset -2px -2px 5px rgb(255, 255, 255), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
}
section.dday article.dday-list .filter-form fieldset input:focus {
  background: rgba(255, 255, 255, 0.8);
}
section.dday article.dday-list .filter-form fieldset input::placeholder {
  color: inherit;
  opacity: 0.5;
}
section.dday article.dday-list .filter-form fieldset input::-webkit-calendar-picker-indicator {
  filter: invert(10%);
  mix-blend-mode: color-burn;
}
section.dday article.dday-list .no-msg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  text-align: center;
  font-weight: 600;
  font-size: 1.2em;
}
section.dday .btn-add {
  width: 100%;
  padding: 10px 0;
  outline: none;
  border-radius: 10px;
  box-shadow: -2px -2px 5px rgb(255, 255, 255), 3px 3px 5px rgba(0, 0, 0, 0.1);
}
section.dday .btn-add img {
  width: 10%;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  mix-blend-mode: overlay;
  filter: grayscale(1) brightness(50%);
}
section.dday .btn-add:focus {
  background: rgba(255, 255, 255, 0.1);
}
section.dday .btn-add:active {
  box-shadow: inset -2px -2px 5px rgb(255, 255, 255), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
}
section.dday form.dday-add {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
section.dday form.dday-add p {
  padding: 15px 0;
  border-top: 2px groove rgba(255, 255, 255, 0.5);
}
section.dday form.dday-add p i {
  padding: 0 10px;
}
section.dday form.dday-add p label {
  display: block;
  margin-bottom: 10px;
}
section.dday form.dday-add p input {
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  border: none;
  background: rgba(255, 255, 255, 0.4);
  outline: none;
  box-shadow: inset -2px -2px 5px rgb(255, 255, 255), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
}
section.dday form.dday-add p input:focus {
  background: rgba(255, 255, 255, 0.8);
}
section.dday form.dday-add p input::placeholder {
  color: inherit;
  opacity: 0.5;
}
section.dday form.dday-add p input::-webkit-calendar-picker-indicator {
  filter: invert(10%);
  mix-blend-mode: color-burn;
}
section.dday form.dday-add button {
  width: 100%;
  margin-top: 15px;
  padding: 10px 0;
  outline: none;
  border-radius: 20px;
  box-shadow: -2px -2px 5px rgb(255, 255, 255), 3px 3px 5px rgba(0, 0, 0, 0.1);
}
section.dday form.dday-add button:focus {
  background: rgba(255, 255, 255, 0.1);
}
section.dday form.dday-add button:active {
  box-shadow: inset -2px -2px 5px rgb(255, 255, 255), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
}
section.dday form.dday-add .fa-arrow-left {
  font-size: 1.5em;
  display: block;
  margin: 0 auto;
  cursor: pointer;
  margin-top: 20px;
  transition: color 0.8s;
}
section.dday form.dday-add .fa-arrow-left:active {
  transform: scale(1.2);
}
section.dday form.dday-add .fa-arrow-left:hover {
  color: #FFF;
}
section.dday div.now {
  text-align: center;
  padding: 10px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 7px;
  color: #fff;
}
section.dday div.now time {
  display: block;
}
section.dday div.now time b,
section.dday div.now time small {
  margin: 0 3px;
}
section.dday div.now time small {
  font-size: 0.9em;
  font-weight: 600;
  text-transform: uppercase;
  opacity: 0.8;
}