#root section.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  box-sizing: border-box;
  padding: 10px;
}
#root section.menu h1 {
  margin-bottom: 60px;
}
#root form.theme legend {
  margin-bottom: 15px;
  font-weight: 800;
  font-size: 0.8em;
  text-align: center;
}
#root form.theme div {
  display: flex;
  width: 180px;
  height: 30px;
  border-radius: 30px;
  background: #ccc;
  box-shadow: -2px -2px 5px rgb(255, 255, 255), 3px 3px 5px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}
#root form.theme div input {
  display: none;
}
#root form.theme div input:checked + label {
  opacity: 1;
}
#root form.theme div input:checked + label::before {
  display: block;
}
#root form.theme div label {
  position: relative;
  flex: 1;
  cursor: pointer;
  opacity: 0.3;
}
#root form.theme div label::before {
  content: "\f00c";
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
}
#root form.theme div label:hover {
  opacity: 1;
}
#root form.theme div label[for=radio1] {
  background: #999;
}
#root form.theme div label[for=radio2] {
  background: rgb(178, 211, 255);
}
#root form.theme div label[for=radio3] {
  background: rgb(255, 202, 202);
}
#root form.theme div label::before {
  display: none;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
}
#root nav {
  margin-top: 40px;
  text-transform: uppercase;
  font-weight: 700;
}
#root nav a {
  display: block;
  padding: 7px 25px;
  box-shadow: -2px -2px 5px rgb(255, 255, 255), 3px 3px 5px rgba(0, 0, 0, 0.1);
  margin: 20px 0;
  border-radius: 20px;
}
#root nav a:hover {
  text-shadow: -2px -2px 1px rgb(255, 255, 255), 2px 2px 1px rgba(0, 0, 0, 0.1);
}
#root nav a:active {
  box-shadow: -2px -2px 5px rgb(136, 136, 136), 3px 3px 5px rgb(255, 255, 255);
}
#root nav a i {
  margin: 10px;
}