section.calc {
    display: flex;
    height: 100%;
    flex-direction: column;
    p.output {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 150px;
        box-sizing: border-box;
        padding: 15px;
        margin-bottom: 15px;
        border: 1px solid rgba(#fff, 0.3);
        box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
        font-size: 1.7em;
        font-weight: 600;
        letter-spacing: 0.2em;
        background: rgba(#fff, 0.3);
        span{
            position: relative;
            overflow-wrap: break-word;
            word-break: break-all;
        }

        span::after {
            content: "|";
            font-weight: 100;
            animation: cursor 0.3s infinite;
        }
    }
    ul.btn-container {
        height: 65%;
        flex: 1;
        display: flex;
        flex-wrap: wrap;
        &:hover {
            font-size: 0.8em;
        }
        li {
            width: 25%;
            height: 20%;
            box-sizing: border-box;
            padding: 7px;
            button {
                font-size: 1.8em;
                font-weight: 900;
                border-radius: 2px;
                width: 100%;
                height: 100%;
                box-shadow: -2px -2px 2px rgba(255, 255, 255, 1), 2px 2px 2px rgba(0, 0, 0, 0.1);
                transition: font-size 0.2s;
                &:active {
                    //누르고 있는 동안
                    box-shadow: -2px -2px 2px rgba(#000, 0.1), 2px 2px 2px rgba(#fff, 1);
                }
                &:hover {
                    font-size: 1.3em;
                }
            }
        }
    } 
    .calc-modal{
        position: fixed;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        color: #FFF;
        background: rgba(#000,0.7);
        font-size: 1.2em;
    }
} //section
