@use "./mixin" as m;

#root {
    section.menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 100%;
        box-sizing: border-box;
        padding: 10px;
        h1{
            margin-bottom: 60px;
        }
    } //section
    form.theme {
        legend {
            margin-bottom: 15px;
            font-weight: 800;
            font-size: 0.8em;
            text-align: center;
        }
        div {
            display: flex;
            width: 180px;
            height: 30px;
            border-radius: 30px;
            background: #ccc;
            box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
            overflow: hidden;

            input {
                display: none;
                &:checked + label{
                    opacity: 1;
                }
                &:checked + label::before {
                    display: block;
                } //체크가된인풋의 뒤에있는 요소
            } //input
            label {
                position: relative;
                flex: 1;
                cursor: pointer;
                @include m.icon(before, "\f00c");
                opacity: 0.3;
                &:hover {
                    opacity: 1;
                }
                &[for="radio1"] {
                    background: #999;
                }
                &[for="radio2"] {
                    background: rgb(178, 211, 255);
                }
                &[for="radio3"] {
                    background: rgb(255, 202, 202);
                }
                &::before {
                    display: none;
                    @include m.posLT(absolute, 50%, 50%, -50%, -50%);
                    color: #fff;
                }
            } //label
        }
    } //theme
    nav {
        margin-top: 40px;
        text-transform: uppercase;
        font-weight: 700;
        a {
            display: block;
            //text-align: center;
            padding: 7px 25px;
            box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
            margin: 20px 0;
            border-radius: 20px;
            &:hover {
                text-shadow: -2px -2px 1px rgba(255, 255, 255, 1), 2px 2px 1px rgba(0, 0, 0, 0.1);
            }
            &:active {
                box-shadow: -2px -2px 5px rgb(136, 136, 136), 3px 3px 5px rgba(255, 255, 255, 1);
            }
            i{
                margin: 10px;
            }
        }
    }
}
