@use "mixin" as m;
section.bmi {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    form {
        div.radios {
            p {
                display: flex;
                position: relative;
                input {
                    /* display: none 을 하면 reqiured가 접근을 못 함 */
                    @include m.posLT(absolute, 50%, 50%, -50%, -50%);
                    border: none;
                    background: none;
                    opacity: 0;
                    &:checked + label {
                        font-weight: 600;
                        text-shadow: inset 1px 1px 1px rgba(255, 255, 255, 1), inset -1px -1px 1px rgba(0, 0, 0, 0.1);
                        box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
                    }
                } //input
                label {
                    flex: 1;
                    padding: 10px 0;
                    text-align: center;
                    cursor: pointer;
                    box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);

                    &:first-of-type {
                        border-radius: 30px 0 0 30px;
                    }
                    &:last-of-type {
                        border-radius: 0 30px 30px 0;
                    }
                } //label
            } //p
        } //div.radios
        > div {
            padding: 15px 0;
            border-bottom: 1px groove rgba(#000, 0.1);
            > label {
                display: block;
                margin-bottom: 10px;
            }
            input[type="number"] {
                width: 100%;
                box-sizing: border-box;
                padding: 10px;
                border: none;
                background: rgba(#fff, 0.4);
                outline: none;
                box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
                &:focus {
                    background: rgba(#fff, 0.8);
                }
                &::placeholder {
                    color: inherit;
                    opacity: .5;
                }
            } //input[type=number]
        } //div
        button {
            width: 100%;
            padding: 10px 0;
            outline: none;
            box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
            &:focus {
                background: rgba(#fff, 0.2);
            }
            &:active {
                box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
            } //active
        } //button
    } //form

    div.bmi-result {
        position: absolute;
        left: 100%;
        top: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        box-sizing: border-box;
        padding: 15px;
        width: 100%;
        height: 100%;
        background: rgba(#fff, 0.7);
        transition: all 0.3s;
        &.active {
            left: 0;
        }

        .output {
            position: relative;
            height: 75%;
            margin-bottom: 30px;
            background: rgba(#fff, 0.5);
            border: 1px solid #eee;

            &.result {
                background: #FFF;
                p{
                    font-size: 3em;
                    @include m.posLT(absolute,50%,50%,-50%,-50%);
                    opacity: 0.7;
                }
            }//output result
            &.err {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                p{
                    position: relative;
                    margin: 20px 0;
                   i{
                    color: rgba(#FFF,.5);
                    font-size: 8em;
                    }//i
                    i:last-child{
                        font-size: 15em;
                        @include m.posLT(absolute,50%,50%,-50%,-50%);
                    }
                }//p
                background: #000
            }//output err

            svg.morph {
                @include m.posLT(absolute, 50%, 50%, -50%, -50%);
                height: 100%;
                .org {
                    fill: #8facca;
                }
            } //svg
            .board {
                position: absolute;
                width: 100%; //view box 해둬서 높이는 자동
                bottom: 0;
                img:first-child{
                    opacity: 80%;
                }
                .pin {
                    position: absolute;
                    bottom: 0;
                    right: 50%;
                    width: 30%;
                    //opacity: 100%;
                    transform-origin: 93% center;
                    transition: transform 1s cubic-bezier(0.64, 0.57, 0.67, 1.53);
                } //pin
            } //board figure
        } //output

        button {
            padding: 10px;
            background: rgba(#fff, 0.5);
            border: 2px solid #fff;
            box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
            &:active {
                box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
            } //button:active
        } //button
    } //div.bmi-result
} //section
