figure.coach-mark {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #5f5f5f;
}
figure.coach-mark.active {
  opacity: 0;
  transition: opacity 0.5s;
}
figure.coach-mark img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}
figure.coach-mark figcaption {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 3%;
  transform: translate(0, 0);
  text-align: center;
  color: #FFF;
}
figure.coach-mark figcaption button {
  margin-top: 5px;
  padding: 10px 40px;
  border: 1px solid #FFF;
  text-transform: uppercase;
  transition: all 0.3s;
  background: rgba(255, 255, 255, 0.2);
}
figure.coach-mark figcaption button:hover, figure.coach-mark figcaption button:active {
  background: #FFF;
  color: #111;
}